import { Component, TemplateRef, OnInit, ViewChild } from '@angular/core';
import { Wire } from './wire';
import { WireService } from '../wire.service';

@Component({
  selector: 'make-wire',
  templateUrl: '../components/html/makewire.component.html',
  providers: [WireService]
})

export class MakeWireComponent {
  @ViewChild('readonlytemp', { static: false })
  readonlytemp: TemplateRef<any>;

  @ViewChild('editabletemp', { static: false })
  editabletemp: TemplateRef<any>;

  OnInit() {
    this.LoadWires();
  }

  wires: Array<Wire>;
  
  editWire: Wire | null=null;
  isnew: boolean = false;
  status: string = "";

  constructor(private serv: WireService) {
    this.wires = new Array<Wire>();
  }

  LoadTemlate(wire: Wire) {

    if (this.editWire != null && this.editWire._id == wire._id) {

      return this.editabletemp;

    }
    return this.readonlytemp;

  }

  private LoadWires() {
    this.serv.getWires().subscribe((data: Array<Wire>) => {
      this.wires = data;
    })
  }

  AddWire() {

    this.editWire = new Wire("", "", "", "", 0);
    this.wires.push(this.editWire);
    this.isnew = true;

  }

  EditWire(wire: Wire) {

    this.editWire = new Wire(wire._id, wire.name, wire.firstconn, wire.secondconn, wire.length);

  }




  Save() {

    if (this.isnew) {

      this.PostWire(this.editWire as Wire);
      this.isnew = false;

    }
    else {

      this.PutWire(this.editWire as Wire);

    }
    this.editWire = null;

  }

  PutWire(putwire: Wire) {

    this.serv.editWire(putwire).subscribe((data: Wire) => {

      this.LoadWires();
      this.status = "Успешно отредактировано";

    })

  }

  PostWire(postwire: Wire) {

    this.serv.postWire(postwire).subscribe((data: Wire) => {

      this.LoadWires();
      this.status = "Успешно добавлено";

    })

  }


  DeleteWire(delwire:Wire) {

    this.serv.deleteWire(delwire._id).subscribe(_ => {

      this.status = "Успешно удалено";
      this.LoadWires();

    })

  }

  CancelEdit() {



  }

}
